* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

span {
  font-size: 20px;
  padding: 8px;
}

.country-info {
  font-size: 20px;
  padding-left: 40px;
  margin-top: 16px;
  margin-bottom: 16px;
  color: black;
}

.info-heading {
  font-size: 20px;
  color: blue;
}
